.header {
  display: flex;
  align-items: center;
  background-color: #389ef2;
  height: 60px;
  font-family: RobotoCondensedBI, sans-serif;
  color: #FFFFFF;

  img{
    align-self: center;
  }

  a {
    display: flex;
    text-decoration: none;
    color: #FFFFFF;
    font-size: 22px;

    span {
      padding: 0px 10px;
      align-self: center;

    }
  }

  .container {
    display: flex;
    justify-content: space-between;
  }
  .header-logo {
    display: flex;
    font-style: italic;
    img {
      width: 25px;
    }
    a, span{
      align-self: center;
    }
    a {
      border-right: 2px solid #ffc107;
      font-size: 25px;
    }
  }

  .header-text {
    font-size: 15px;
    margin-left: 10px;
    text-transform: uppercase;
  }

  .header-user__nav {
    display: flex;
  }

  .header-signup {
    margin-left: 30px;
    img {
      width: 25px;
      height: 18px;
    }

  }


  .header-login {
    margin-left: 30px;
    img {
      width: 35px;
      height: 35px;
    }
  }


  .header-about {
    margin-left: 30px;
    opacity: 0.6;
    &:hover {
      opacity: 1;
      transition: 0.5s;
    }
    img {
      width: 30px;
    }
  }
}
